<template>
  <div>
    <BaseChar
      v-for="(t, i) in text"
      :key="i"
      :char="t"
      :ctrl="cs[i]"
      @click="toggle(i)"
      @mouseenter="emit('show')"
    />
  </div>
</template>

<script setup>
import { computed, ref, watch } from "vue";
import BaseChar from "./BaseChar.vue";
const props = defineProps({
  text: String,
  ctrl: String,
});
const emit = defineEmits(["update:ctrl", "show"]);

const col = computed(() => {
  return props.text.length;
});

const cs = ref([]);

const format_str = (str, len) => {
  let pre = new Array(len).fill(0).join("");
  str = pre + str;
  return str.slice(-len);
};

watch(
  () => props.ctrl,
  () => {
    let t = format_str(parseInt(props.ctrl, 16).toString(2), col.value);
    let ts = t.split("");
    ts.forEach((t, i) => {
      cs.value[i] = t;
    });
  },
  { immediate: true }
);

const toggle = (i) => {
  cs.value[i] = cs.value[i] == "1" ? "0" : "1";
  let t = cs.value.join("");
  emit("update:ctrl", parseInt(t, 2).toString(16));
};
</script>
