<template>
  <div class="char" :class="show" ref="char_ref">{{ char }}</div>
</template>

<script setup>
import { watch, ref } from "vue";
const props = defineProps({
  char: String,
  ctrl: String,
});
const show = ref("off");
const char_ref = ref(null);
watch(
  () => props.ctrl,
  (value) => {
    show.value = value == "1" ? "on" : "off";
    // 重新刷新动画
    char_ref.value.style.animation = "none";
    char_ref.value.offsetHeight;
    char_ref.value.style.animation = "";
  }
);
</script>

<style scoped>
@keyframes moveup {
  from {
    background-color: #eee;
    color: #222;
  }
  to {
    background-color: #222;
    color: #eee;
  }
}

@keyframes movedown {
  to {
    background-color: #eee;
    color: #222;
  }
  from {
    background-color: #222;
    color: #eee;
  }
}

.off {
  animation: moveup 1s 1 both;
}

.on {
  animation: movedown 1s 1 both;
}

.char {
  margin: 4px;
  padding: min(6px, 2%) min(10px, 2%);
  font-family: Consolas;
  font-size: 16px;
  display: inline-block;
  cursor: pointer;
  user-select: none;
}
</style>