<template>
  <div class="clock" @click="update">
    <BaseLine
      v-for="(t, i) in text_list"
      :key="i"
      :text="t"
      v-model:ctrl="ctrl_list[i]"
      @show="update"
    />
  </div>
</template>

<script setup>
import { ref } from "vue";
import BaseLine from "./BaseLine.vue";

let text_list = `ITLISASAMPM
ACQUARTERDC
TWENTYFIVEX
HALFBTENFTO
PASTERUNINE
ONESIXTHREE
FOURFIVETWO
EIGHTELEVEN
SEVENTWELVE
TENSEOCLOCK`.split("\n");

let row = text_list.length;
const ctrl_list = ref(new Array(row).fill("0"));

const hex_add = (hex1, hex2) => {
  let t = parseInt(hex1, 16) + parseInt(hex2, 16);
  return t.toString(16);
};

const list_deal = (list, row, add) => {
  list[row] = hex_add(list[row], add);
};

let show = false;
let timer = 0;

const clear = () => {
  show = false;
  ctrl_list.value = new Array(row).fill("0");
};

const update = () => {
  let date = new Date();
  let h = date.getHours();
  let m = date.getMinutes();

  show = true;

  let list = new Array(row).fill("0");

  // 分钟
  // 提前
  if (m >= 35) {
    list_deal(list, 3, "3");
    h = (h + 1) % 24;
    if (m < 40) list_deal(list, 2, "7fe");
    else if (m < 45) list_deal(list, 2, "7e0");
    else if (m < 50) list_deal(list, 1, "5fc");
    else if (m < 55) list_deal(list, 3, "38");
    else list_deal(list, 2, "1e");
  }
  // 退后
  else if (m >= 5) {
    list_deal(list, 4, "780");
    if (m < 10) list_deal(list, 2, "1e");
    else if (m < 15) list_deal(list, 3, "38");
    else if (m < 20) list_deal(list, 1, "5fc");
    else if (m < 25) list_deal(list, 2, "7e0");
    else if (m < 30) list_deal(list, 2, "7fe");
    else list_deal(list, 3, "780");
  } else {
    list_deal(list, 9, "3f");
  }

  // 12时
  if (h >= 12) list_deal(list, 0, "6c3");
  else list_deal(list, 0, "6cc");

  // 小时
  h = h % 12;
  if (h == 0) list_deal(list, 8, "3f");
  else if (h == 1) list_deal(list, 5, "700");
  else if (h == 2) list_deal(list, 6, "7");
  else if (h == 3) list_deal(list, 5, "1f");
  else if (h == 4) list_deal(list, 6, "780");
  else if (h == 5) list_deal(list, 6, "78");
  else if (h == 6) list_deal(list, 5, "e0");
  else if (h == 7) list_deal(list, 8, "7c0");
  else if (h == 8) list_deal(list, 7, "7c0");
  else if (h == 9) list_deal(list, 4, "f");
  else if (h == 10) list_deal(list, 9, "700");
  else list_deal(list, 7, "3f");

  ctrl_list.value = list;

  if (timer) {
    clearTimeout(timer);
  }
  timer = setTimeout(() => {
    if (show) clear();
  }, 5000);
};
</script>

<style scoped>
.clock {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}
</style>

